'use client';

import { useState, useEffect } from 'react';
import { collection, query, where, getDocs, Timestamp, updateDoc, doc } from 'firebase/firestore';
import { db } from '@/src/firebaseConfig';
import Header from '@/src/components/landingpage/Header';
import Footer from '@/src/components/landingpage/Footer';
import { Card, CardContent } from '@/src/components/ui/card';
import { Button } from '@/src/components/ui/button';
import ExtendRentalModal from '@/src/components/return/ExtendRentalModal';
import SwitchBikeModal from '@/src/components/return/SwitchBikeModal';
import { Bike, User, Calendar, PhoneCall, Coins, Search, CheckCircle, AlertCircle } from 'lucide-react';
import { Input } from '@/src/components/ui/input';
import { Booking } from '@/src/lib/booking/BookingTypes';
import { format } from 'date-fns';

export default function ReturnBikePage() {
  const [rentalInfo, setRentalInfo] = useState<any>(null);
  const [extendOpen, setExtendOpen] = useState(false);
  const [switchOpen, setSwitchOpen] = useState(false);
  const [vehicleIdInput, setVehicleIdInput] = useState('');
  const [bookingDocId, setBookingDocId] = useState<string | null>(null);
  const [successMessage, setSuccessMessage] = useState('');
  const [errorMessage, setErrorMessage] = useState('');
  const [suggestions, setSuggestions] = useState<Booking[]>([]);

  useEffect(() => {
    const fetchSuggestions = async () => {
      if (vehicleIdInput.length < 2) return setSuggestions([]);

      const bookingsRef = collection(db, 'bookings');
      const q = query(bookingsRef, where('bookingStatus', '==', 'confirmed'));
      const snap = await getDocs(q);
      const matches: Booking[] = [];

      snap.forEach((docSnap) => {
        const data = docSnap.data() as Booking;
        if (
          data.vin.toLowerCase().includes(vehicleIdInput.toLowerCase()) ||
          (data.licensePlate && data.licensePlate.toLowerCase().includes(vehicleIdInput.toLowerCase()))
        ) {
          matches.push({ ...data, id: docSnap.id });
        }
      });

      setSuggestions(matches);
    };

    fetchSuggestions();
  }, [vehicleIdInput]);

  const handleSelectSuggestion = (booking: Booking) => {
    setRentalInfo(booking);
    setBookingDocId(booking.id);
    setSuggestions([]);
  };

  const handleReturnBike = async () => {
    if (!bookingDocId || !rentalInfo) return;

    if (rentalInfo.remainingBalance > 0) {
      setErrorMessage('Customer must pay remaining balance before returning.');
      return;
    }

    const bookingRef = doc(db, 'bookings', bookingDocId);
    await updateDoc(bookingRef, {
      bookingStatus: 'completed',
      updatedAt: Timestamp.now(),
      statusComment: 'Returned on ' + new Date().toLocaleString(),
    });

    const ebikesRef = collection(db, 'ebikes');
    const ebikeQuery = query(ebikesRef, where('vehicleID', '==', rentalInfo.vin));
    const ebikeSnap = await getDocs(ebikeQuery);

    if (!ebikeSnap.empty) {
      const ebikeDoc = ebikeSnap.docs[0];
      await updateDoc(doc(db, 'ebikes', ebikeDoc.id), {
        status: 'available',
        updatedAt: Timestamp.now(),
      });
    }

    setSuccessMessage('Bike has been successfully returned and marked as available.');
    setRentalInfo(null);
    setBookingDocId(null);
    setVehicleIdInput('');
  };

  const handleExtendConfirm = (newEndTime: string) => {
    console.log('New end time selected:', newEndTime);
  };

  const handleSwitchConfirm = (newBike: any) => {
    console.log('Switched to bike:', newBike);
  };

  const formatDateTime = (dateStr?: string, hourStr?: string) => {
    if (!dateStr) return '-';
    try {
      const full = hourStr ? `${dateStr}T${hourStr}` : `${dateStr}T00:00`;
      return format(new Date(full), 'dd/MM/yyyy HH:mm');
    } catch {
      return '-';
    }
  };

  return (
    <div className="min-h-screen flex flex-col bg-gray-100">
      <Header />
      <main className="flex-1 py-10 px-4">
        <h1 className="text-3xl font-bold text-center mb-8">Return Bike</h1>

        <div className="max-w-2xl mx-auto space-y-8">
          <Card className="shadow-md rounded-xl">
            <CardContent className="p-5 space-y-4">
              <h2 className="text-xl font-semibold flex items-center gap-2">
                <Bike className="w-5 h-5 text-[#00d289]" /> Search by VIN or Plate Number
              </h2>
              <div className="relative">
                <Input
                  placeholder="Type at least 2 characters..."
                  value={vehicleIdInput}
                  onChange={(e) => setVehicleIdInput(e.target.value)}
                />
                {suggestions.length > 0 && (
                  <ul className="absolute z-10 bg-white shadow-lg w-full border rounded mt-1 max-h-60 overflow-y-auto">
                    {suggestions.map((sug) => (
                      <li
                        key={sug.id}
                        className="px-4 py-2 cursor-pointer hover:bg-gray-100 text-sm"
                        onClick={() => handleSelectSuggestion(sug)}
                      >
                        {sug.vin} {sug.licensePlate ? `- ${sug.licensePlate}` : ''}
                      </li>
                    ))}
                  </ul>
                )}
              </div>
              {successMessage && (
                <div className="text-green-600 text-sm flex items-center gap-1 mt-2">
                  <CheckCircle className="w-4 h-4" /> {successMessage}
                </div>
              )}
              {errorMessage && (
                <div className="text-red-600 text-sm flex items-center gap-1 mt-2">
                  <AlertCircle className="w-4 h-4" /> {errorMessage}
                </div>
              )}
            </CardContent>
          </Card>

          {rentalInfo && (
            <Card className="shadow-md rounded-xl">
              <CardContent className="p-5 space-y-4 text-sm text-gray-800">
                <div className="flex items-center gap-2">
                  <User className="w-4 h-4 text-[#00d289]" /> <strong>Renter:</strong> {rentalInfo.fullName}
                </div>
                <div className="flex items-center gap-2">
                  <PhoneCall className="w-4 h-4 text-[#00d289]" /> <strong>Phone:</strong> {rentalInfo.phone}
                </div>
                <div className="flex items-center gap-2">
                  <Calendar className="w-4 h-4 text-[#00d289]" /> <strong>Start:</strong> {formatDateTime(rentalInfo.rentalStartDate, rentalInfo.rentalStartHour)}
                </div>
                <div className="flex items-center gap-2">
                  <Calendar className="w-4 h-4 text-[#00d289]" /> <strong>End:</strong> {formatDateTime(rentalInfo.rentalEndDate)}
                </div>
                <div className="flex items-center gap-2">
                  <Coins className="w-4 h-4 text-[#00d289]" /> <strong>Total:</strong> {rentalInfo.totalAmount.toLocaleString()}‚Ç´
                </div>
                <div className="flex items-center gap-2">
                  <Coins className="w-4 h-4 text-[#00d289]" /> <strong>Deposit:</strong> {rentalInfo.deposit.toLocaleString()}‚Ç´
                </div>
                <div className="flex items-center gap-2">
                  <Coins className="w-4 h-4 text-[#00d289]" /> <strong>Remaining:</strong> {rentalInfo.remainingBalance.toLocaleString()}‚Ç´
                </div>

                <div className="flex justify-between mt-6 flex-wrap gap-2">
                  <Button variant="outline" onClick={() => setExtendOpen(true)}>
                    Extend Rental
                  </Button>
                  <Button variant="secondary" onClick={() => setSwitchOpen(true)}>
                    Switch Bike
                  </Button>
                  <Button variant="default" onClick={handleReturnBike}>
                    Return Bike
                  </Button>
                </div>
              </CardContent>
            </Card>
          )}

          <ExtendRentalModal
            open={extendOpen}
            onClose={() => setExtendOpen(false)}
            onConfirm={handleExtendConfirm}
            currentEndTime={rentalInfo?.rentalEndDate ? rentalInfo.rentalEndDate : ''}
          />

          <SwitchBikeModal
            open={switchOpen}
            onClose={() => setSwitchOpen(false)}
            onConfirm={handleSwitchConfirm}
          />
        </div>
      </main>
      <Footer />
    </div>
  );
}
