'use client';

import { useState } from 'react';
import { Dialog, DialogContent, DialogHeader, DialogTitle } from '@/src/components/ui/dialog';
import { Label } from '@/src/components/ui/label';
import { Input } from '@/src/components/ui/input';
import { Button } from '@/src/components/ui/button';

interface ExtendRentalModalProps {
  open: boolean;
  onClose: () => void;
  onConfirm: (newEndTime: string) => void;
  currentEndTime?: string;
}

export default function ExtendRentalModal({ open, onClose, onConfirm, currentEndTime }: ExtendRentalModalProps) {
  const [newTime, setNewTime] = useState('');

  const handleSubmit = () => {
    if (!newTime) return;
    onConfirm(newTime);
    onClose();
  };

  return (
    <Dialog open={open} onOpenChange={onClose}>
      <DialogContent className="max-w-md">
        <DialogHeader>
          <DialogTitle className="text-xl font-semibold">Extend Rental Time</DialogTitle>
        </DialogHeader>

        <div className="space-y-6">
          <div>
            <Label htmlFor="newEndTime">New End Time</Label>
            <Input
              id="newEndTime"
              type="datetime-local"
              value={newTime}
              onChange={(e) => setNewTime(e.target.value)}
              className="mt-2 focus:ring-2 focus:ring-[#00d289]"
            />
          </div>

          <div className="flex justify-end gap-3">
            <Button variant="outline" onClick={onClose}>Cancel</Button>
            <Button className="bg-[#00d289] hover:bg-[#00b67a] text-white" onClick={handleSubmit}>Confirm</Button>
          </div>
        </div>
      </DialogContent>
    </Dialog>
  );
}