'use client';

import { useEffect, useState } from 'react';
import { Dialog, DialogContent, DialogHeader, DialogTitle } from '@/src/components/ui/dialog';
import { Button } from '@/src/components/ui/button';
import { ScrollArea } from '@/src/components/ui/scroll-area';
import { collection, getDocs, query, where } from 'firebase/firestore';
import { db } from '@/src/firebaseConfig';
import { useUser } from '@/src/context/AuthContext';

interface Bike {
  id: string;
  model: string;
  plate: string;
  battery: string;
  location: string;
}

interface SwitchBikeModalProps {
  open: boolean;
  onClose: () => void;
  onConfirm: (selectedBike: Bike) => void;
}

export default function SwitchBikeModal({ open, onClose, onConfirm }: SwitchBikeModalProps) {
  const [selectedBike, setSelectedBike] = useState<Bike | null>(null);
  const [availableBikes, setAvailableBikes] = useState<Bike[]>([]);
  const { staffProfile } = useUser();
  const stationId = staffProfile?.stationId;

  useEffect(() => {
    const fetchAvailableBikes = async () => {
      if (!stationId) return;

      const q = query(
        collection(db, 'ebikes'),
        where('status', '==', 'available'),
        where('stationId', '==', stationId)
      );
      const snap = await getDocs(q);
      const list: Bike[] = snap.docs.map((doc) => {
        const data = doc.data();
        return {
          id: doc.id,
          model: data.modelName || 'Unknown Model',
          plate: data.plateNumber || 'No Plate',
          battery: data.batteryCapacity ? `${data.batteryCapacity}%` : 'Unknown',
          location: data.currentLocation || 'No location',
        };
      });
      setAvailableBikes(list);
    };

    if (open) fetchAvailableBikes();
  }, [open, stationId]);

  const handleConfirm = () => {
    if (!selectedBike) return;
    onConfirm(selectedBike);
    onClose();
  };

  return (
    <Dialog open={open} onOpenChange={onClose}>
      <DialogContent className="max-w-md">
        <DialogHeader>
          <DialogTitle className="text-xl font-semibold">Switch to Another Bike</DialogTitle>
        </DialogHeader>

        <ScrollArea className="h-60 space-y-4 pr-2 mt-4">
          {availableBikes.map((bike) => (
            <div
              key={bike.id}
              className={`border p-4 rounded-xl cursor-pointer transition-all ${
                selectedBike?.id === bike.id ? 'border-[#00d289] bg-[#e6fff6]' : 'hover:bg-gray-50'
              }`}
              onClick={() => setSelectedBike(bike)}
            >
              <div className="font-semibold text-sm">{bike.model} - {bike.plate}</div>
              <div className="text-xs text-gray-600">üîã {bike.battery} | üìç {bike.location}</div>
            </div>
          ))}
        </ScrollArea>

        <div className="flex justify-end mt-6 gap-3">
          <Button variant="outline" onClick={onClose}>Cancel</Button>
          <Button
            onClick={handleConfirm}
            disabled={!selectedBike}
            className="bg-[#00d289] hover:bg-[#00b67a] text-white"
          >
            Confirm
          </Button>
        </div>
      </DialogContent>
    </Dialog>
  );
}